module.exports = {
  simple: {
    amountCards: 1,
    className: 'simple',
    description: 'Esta é a tiragem mais simples e rápida. Ela oferece uma visão geral sobre a sua questão.',
    cardsFeatures: {
      1: 'Visão geral'
    }
  },
  period:{
    amountCards: 3,
    className: 'period',
    description: 'Esta tiragem oferece uma visão sobre o passado, presente e futuro da sua questão.',
    cardsFeatures: {
      1: 'Passado',
      2: 'Presente',
      3: 'Futuro'
    }
  }

}



