      description: 'Quando esta carta surge indica que os nossos pensamentos, palavras e ações estão a ser julgados nas esferas sutís, ou seja, Deus (ou seja qual for a ideia que Dele tiver) está a julgar as suas atitudes. Esta carta alerta para a necessidade de colocar a mão na nossa própria consciência. Estaremos a agir bem? Podemos ser julgados também pelos que nos rodeiam, por isso é muito importante que sejamos sinceros e darmos sempre o nosso melhor. O Julgamento pode anunciar mudanças na nossa vida, a nível de estrutura e de bases. Pode indicar um matrimónio ou um divórcio, um contrato, ou o fim dele. Consoante a situação, esta carta indica que chegou ao final de um ciclo, será avaliado e, conforme o seu desempenho, passará a um novo ciclo. A nível amoroso, o Julgamento indica novidades, o início de uma nova fase que será feliz. Avalie os seus sentimentos e procure ser justo e equilibrado. Num relacionamento que enfrenta muitas dificuldades, esta carta indica que deve ponderar esquecer o passado e passar a uma nova fase na sua vida. A nível de saúde esta carta indica que deve fazer exames, análises, um check-up geral. O Julgamento aconselha a que toda a saúde seja analisada para prevenir problemas futuros. No domínio profissional e financeiro, poderá ser promovido, mas terá de prestar provas da sua competência e dos seu valor. Saiba estar à altura dos desafios que lhe apresentam.',
